import React, { Component } from "react";
import { DocumentIcon, PlusCircleIcon } from "@heroicons/react/outline";
import _ from "lodash";

import { connect } from "react-redux";
import {
  AppBackButton,
  AppHeading,
  ClaimStatus,
} from "../components/AppComponents";
import { withRouter } from "react-router";
import Main from "./Main";
// import Button from "../components/Button";
// import { InputRadio } from "../components/form/InputRadio";
import { ApiGet, ROLE_STATUS_MAPPING } from "../lib/AppHelper";
import PaginationData from "./admin/components/PaginationData";

import { Link } from "react-router-dom";
import { AppStore } from "../lib/AppStore";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faEdit,
  faInfoCircle,
  faPlusSquare,
  faRupeeSign,
} from "@fortawesome/free-solid-svg-icons";
import { TagFilter } from "./TagFilter";
import { Popover, Whisper } from "rsuite";
import Button from "@restart/ui/esm/Button";

const DefaultPopover = React.forwardRef(({ content, ...props }, ref) => {
  return (
    <Popover ref={ref} {...props}>
      <p>{content}</p>
    </Popover>
  );
});

const CustomComponent = ({ placement, content, children }) => (
  <Whisper
    trigger="hover"
    placement={"top"}
    controlId={`control-id-${placement}`}
    speaker={<DefaultPopover content={content || "Resubmit Reason"} />}
  >
    <Button appearance="subtle">{children || placement}</Button>
  </Whisper>
);

export class Claims extends React.PureComponent {
  BasicBtn = React.createRef();

  state = {
    status: [],
  };
  setFilterValues = (values) => {
    this.setState(values);
  };
  render() {
    const { user } = this.props;
    const { permissions } = user;

    return (
      <Main MessageRef={this.MessageRef}>
        <div className="grid md:grid-cols-2">
          <div>
            <AppBackButton to="/">Back to Dashboard</AppBackButton>
            <AppHeading className="pl-4">Claims</AppHeading>
          </div>
          {permissions && permissions.claim_add ? (
            <div>
              <Link to="subscriptions">
                <button
                  title="Add Claim"
                  className="float-right bg-transparent hover:bg-red-500 text-red-700 font-semibold hover:text-white py-1 px-3 border border-red-500 hover:border-transparent rounded flex"
                  style={{ alignItems: "center", justifyContent: "center" }}
                >
                  <FontAwesomeIcon icon={faPlusSquare}></FontAwesomeIcon>
                  <p className="px-2"> Add Claim</p>
                </button>
              </Link>
            </div>
          ) : (
            false
          )}
        </div>
        {permissions && permissions.claim_view ? (
          <div className="w-full flex flex-col justify-between space-y-4">
            <div>
              <div className="container my-5 mx-auto px-1">
                <TagFilter
                  setFilterValues={this.setFilterValues}
                  selectedValues={this.state.status}
                  field={"status"}
                  multiple={true}
                />
                <div className="flex flex-wrap content-center">
                  <PaginationData
                    request={ApiGet}
                    renderItem={(item) => <ClaimCard item={item} />}
                    endPoint={"claims"}
                    filterValues={this.state}
                  />
                </div>
              </div>
            </div>
          </div>
        ) : (
          <div className="p-10 text-red-500">
            <h4>You do not have permission to view claim</h4>
          </div>
        )}
      </Main>
    );
  }
}

const mapStateToProps = (state) => {
  const { loading, user } = state;
  return { loading: loading, user: user };
};
const ClaimsConnect = connect(mapStateToProps)((props) => {
  return <Claims {...props} />;
});

export default withRouter(ClaimsConnect);

export const ClaimCard = ({ item }) => {
  const {
    sub_claims,
    claim_id,
    name,
    claim_status,
    claim_reason,
    bill_amount,
    hospital_name,
  } = item;
  console.log(
    "@@@itemitemitem::",
    claim_status,
    ROLE_STATUS_MAPPING[claim_status]
  );

  return (
    <Link to={"/claims/" + claim_id}>
      <div className="mb-5 p-2" style={{ width: 400 }}>
        <article
          className="rounded-lg shadow-lg border-red"
          style={{ minHeight: 280 }}
        >
          <header className="flex items-center justify-between leading-tight p-2 md:p-4">
            <h1 className="text-lg  text-black font-bold text-xl capitalize mr-4 pl-1">
              {name ? name : "Name empty"}
            </h1>
            <p className="text-grey-darker text-sm">
              <ClaimStatus status={claim_status} />
              {/* {claim_status ==="DRAFT" ? (
                <Link to={`claim/${claim_id}`}>
                  <FontAwesomeIcon icon={faEdit} />
                </Link>
              ) : (
                false
              )} */}
            </p>
          </header>
          <body className="text-sm items-center justify-between leading-tight p-4">
            <p className="pt-2">{claim_reason}</p>
            <p className="pt-2">
              {hospital_name ? hospital_name : "Hospital name not available"}
            </p>

            <p className="pt-2 ">
              Total claimed Amount:
              <span className="pl-2">
                <FontAwesomeIcon icon={faRupeeSign} />
                {bill_amount}
              </span>
            </p>
            {sub_claims.map((subClaim) => {
              const {
                sub_claim_id,
                claim_id,
                actual_status,
                product_name,
                remarks,
              } = subClaim;
              return (
                <div
                  className={"flex mt-6 align-center"}
                  style={{ alignItems: "center" }}
                >
                  {product_name}
                  <p className="text-grey-darker text-sm mx-4">
                    <ClaimStatus status={actual_status} />
                    {ROLE_STATUS_MAPPING[actual_status] === "Queried" &&
                      remarks && (
                        <CustomComponent content={remarks}>
                          <FontAwesomeIcon icon={faInfoCircle} />
                        </CustomComponent>
                      )}
                    {ROLE_STATUS_MAPPING[actual_status] === "Queried" ||
                      actual_status === "DRAFT" ? (
                      <Link
                        to={`claimEdit/${claim_id}/${sub_claim_id}`}
                        style={{ marginLeft: 10 }}
                      >
                        <FontAwesomeIcon icon={faEdit} />
                      </Link>
                    ) : (
                      false
                    )}
                  </p>
                </div>
              );
            })}
          </body>
          {/* <footer className="pb-4 my-2 justify-between text-right ">
            <Link to={"/claims/" + claim_id}>
              <button class="text-xs mr-3 bg-transparent hover:bg-red-300  font-semibold hover:text-white py-2 px-4 border border-red-300 hover:border-transparent rounded">
                View Claim
              </button>
            </Link>
          </footer> */}
        </article>
      </div>
    </Link>
  );
};
